* {
  box-sizing: border-box;
}

h1, h2, p, body {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-img {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-flow: column wrap;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__product-photo {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__name:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  width: 100%;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__review {
  color: #060b35;
  align-self: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.card__buy-button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.card__buy-button:hover {
  color: #00acdc;
  background: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: block;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 > :nth-child(-n+1), .stars--2 > :nth-child(-n+2), .stars--3 > :nth-child(-n+3), .stars--4 > :nth-child(-n+4), .stars--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  font-family: Roboto, Helvetica, sans-serif;
  transition-duration: .3s;
}

